
@import url('https://fonts.googleapis.com/css2?family=Kiwi+Maru:wght@300&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  font-family: 'Kiwi Maru', serif;
}
nav{
  z-index: 2;
}
.logo{
  color: greenyellow;
  padding: 0  !important;
}
nav ul li{
  text-transform: uppercase;
}
.navbar-nav > li >a {
  margin: 0 0.7rem;
  color: #fff  !important;
  border-top: 0.1875rem solid rgba(0, 0, 0, 0);
}
 .navbar-nav > li >a.active :focus{
  border-top: 0.1875rem solid greenyellow;
}
.navbar-nav > li >a:hover{
  border-top: 0.1875rem solid greenyellow;
  color: greenyellow !important;
}
.header-wrapper{
  background: url('./mobile.jpg') no-repeat;
  position: relative;
  background-size: cover;
  background-position: center;
  height: 90vh;
}
.main-info{
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Kiwi Maru', serif;
  z-index: 1;
}
.main-info h1{
  text-transform: uppercase;
  color: greenyellow;
}
.typed-text{
  font-size: 2rem;
  color: #fff;
}
.btn-main-offer{
  border: 2px solid rgb(235, 232, 76);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color:  #fff;
  margin: 2.1rem;
}
.btn-main-offer:hover{
  text-decoration: none;
  background-color:greenyellow;
}
.tsparticles-canvas-el{
  height: 100vh;
  position: absolute;
  width: 100%;
  z-index: 1;
}

@media (max-width: 768px){
  .particles{
    display: none;
  }
  .main-info h1{
    font-size: 1.5rem  !important;
  }
}